<script>
	import clsx from 'clsx';

	let className = '';
	export {className as class};
	export let disabled = false;
	export let active = false;
	export let href;

	$: classes = clsx(
		className,
		'nav-link',
		{
			disabled,
			active
		},
	);

	function handleClick(e){
		if (disabled) {
			e.preventDefault();
			e.stopImmediatePropagation();
			return;
		}

		if (href === '#') {
			e.preventDefault();
		}
	}
</script>

<a
	{href}
	on:click on:click="{handleClick}"
	class="{classes}"
>
	<slot/>
</a>
